body{
  margin: 0;
}

.App {
  text-align: center;
  
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.images-list{
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;
  justify-content: center;
}

.images-card{
  width: 20vw;
  margin: 1rem;
  color: white;
  position: relative;
}

.image-size{
  height: 40vh;
  width: 20vw;
  
}
.ImgFullSize{
  height: 100vh;
  width: 100vw;
}



@media screen and (max-width: 400px){

  .images-card{
    width: 40vw;
    margin: 0.1rem;
  } 
  .image-size{
    width: 40vw;
  } 
  .movies-button{
    width: 80%;
  }
}